import formatDate from '../../common/js/date.js';
import throttle from "../../common/js/utils.js"
import {
	findAllDyn,
	getImgTemp,
	getComment,
	addComment,
	concern,
	like,
	likeCount,
	getBullet,
	reply
} from '../../api/api.js';

const MAX_DM_COUNT = 6;
const CHANNEL_COUNT = 10;
export default {

	data() {
		return {
			dynInfo: '',
			commentInfo: '',
			bulletList: [],
			following: false,
			did: 0, // 日志id
			uid: 0, // 用户id
			cid: 0, // 评论用户id
			like: 0,
			beStatus: false,
			commCount: '',
			commContent: '',
			commplaceholder: '為保證用戶隱私，只可以看自己的評論',
			addType: '', // 发送按钮请求不同接口
			array: ['取消關注', '屏蔽用戶', '舉報用戶'],
			pickIndex: 0,
			likeNumber: 0,
			showComment: false,
			count: 10,
			loading: 1,
			flog: true,
			time: '',
			disabled: false,
			isLove: false,
			animation: false,
			height: '',
			doomm: true,
		}
	},
	methods: {
		// 日志详情
		findDyn(loadTime) {
			findAllDyn(0).then(data => {
				this.dynInfo = data
				this.commCount = data[0].com_count
				console.log(data)
				// this.likeNumber = 0; //重置点赞次数
				// 第一个特殊，因为需要滑动后才能监听到swiperChange，所以第一个直接赋值
				if (loadTime == 1) {
					this.did = this.dynInfo[0].id;
					this.uid = this.dynInfo[0].uid;
				}
				this.getAllBullet()
				likeCount(this.did).then(data => {
					this.like = data;
				})
			});
			setTimeout(function() {
				uni.hideLoading();
			}, 800);
		},
		//下拉刷新
		loadFindDyn(num) {
			findAllDyn(this.count).then(data => {
				this.count += data.length
				this.dynInfo = this.dynInfo.concat(data)
				if(data.length == 0){
					uni.showToast({
						icon: 'none',
						title: '没有更多数据了'
					});
				}
			})
		},
		// 返回
		goBack() {
			uni.switchTab({
				url: "../main/main"
			});
		},

		publishDyn() {
			getImgTemp().then(data => {
				this.$store.commit('setImgTemp', data);
				console.log(this.$store.state.imgTemp);
				uni.navigateTo({
					url: 'publish'
				});
				this.findDyn(1);
			});
		},
		// 关注用户
		userFollow() {
			if (this.following == false) {
				concern(1, this.uid).then(data => {
					this.following = true;
					this.findDyn(2);
				});
			} else this.following = false;
		},
		// 举报功能等实现
		bindPickerChange(e) {
			// console.log('picker发送选择改变，携带值为', e.target.value);
			this.pickIndex = e.target.value;
			if (e.target.value == 0) { //取消关注
				this.following = false;
				console.log(this.uid);
				concern(2, this.uid).then(data => {
					this.findDyn();
				});
			}
		},
		//
		behaviour() {
			if (this.beStatus == false) this.beStatus = true;
			else if (this.beStatus == true) this.beStatus = false;
			// console.log(this.beStatus);
		},
		// 用户详情
		userInfo() {
			uni.navigateTo({
				url: '/pages/daily/userInfo?uid=' + this.uid
			});
		},
		// 点赞
		likePerson(isOwn, index) {
			// console.log(index)
			this.dynInfo[index].isLike = 1
			if (isOwn == 1) {
				uni.showToast({
					icon: 'none',
					title: '不可以給自己點贊哦'
				});
			} else {
				this.likeNumber++;
				uni.showToast({
					icon: 'none',
					title: '點贊了' + this.likeNumber + '次'
				});

				like(this.did, 1).then(data => {
					// console.log(data)
					likeCount(this.did).then(data => {
						this.like = data
						this.animation = true

					})
				});
				this.animation = false

			}
		},
		hid() {
			if (!this.flog) {
				this.showComment = false
			}
		},
		// 展示评论
		showComm() {
			(this.showComment == true) ? (this.showComment = false) : (this.showComment = true);
			getComment(this.did).then(data => {
				this.commentInfo = data;
				console.log(this.commentInfo);
			});
			this.flog = false
		},
		hidComm() {
			(this.showComment == true) ? (this.showComment = false) : (this.showComment = true);
		},
		moveHandle() {},
		// 获取swiper滚动后日志值
		swiperChange(e) {
			this.following = false; // 重置关注按钮状态
			this.showComment = false; // 重置评论样式
			this.addType = ''; // 重置发送按钮类型
			this.commplaceholder = '為保證用戶隱私，只可以看自己的評論'; // 清除占位符
			this.commentInfo = '' // 清空评论
			this.likeNumber = 0;
			this.isLove = false
			this.did = this.dynInfo[e.detail.current].id;
			this.uid = this.dynInfo[e.detail.current].uid;
			this.commCount = this.dynInfo[e.detail.current].com_count;
			//获取点赞数
			likeCount(this.did).then(data => {
				this.like = data
			})
			// 获取弹幕
			this.getAllBullet()
			let mun =  this.dynInfo.length
			if (e.detail.current + 1 == this.count) {
				console.log("上拉")
				this.loading = 2
				this.loadFindDyn(mun)
			}
			if (e.detail.current == 0) {
				this.findDyn(1);

			}
		},
		// 评论
		addComm() {
			// console.log(this.did);
			this.disabled = true
			if (this.commContent != '') {
				if (this.addType == 'reply') {
					console.log('刚刚是回复');
					// console.log('cid' + this.cid);
					// console.log('commContent' + this.commContent);
					reply(this.cid, this.commContent).then(data => {
						this.findDyn(2);
						this.addType = ''; // 重置发送按钮类型
						this.commplaceholder = '為保證用戶隱私，只可以看自己的評論'; // 清除占位符
						this.disabled = false
					});
				} else {
					console.log('刚刚是評論');
					addComment(this.commContent, this.did).then(data => {
						getComment(this.did).then(data => {
							this.commentInfo = data;
							this.commContent = '';
							this.addType = ''; // 重置发送按钮类型
							this.commplaceholder = '為保證用戶隱私，只可以看自己的評論'; // 清除占位符
							this.disabled = false
						});
						// 获取弹幕
						this.getAllBullet()
						//
						this.findDyn(2)
					});
				}
			}

		},
		// 回复评论
		replyComm(cid, name, comUid) {
			if (comUid != uni.getStorageSync('USERS_KEY').id) {
				this.commplaceholder = '@' + name;
				this.addType = 'reply';
				this.cid = cid;
			} else {
				uni.showToast({
					icon: 'none',
					title: '不能自己回復自己哦'
				})
			}
		},
		//获取弹幕
		getAllBullet() {
			this.bulletList = []
			getBullet(this.did).then(data => {
				// bulletList = data.content;
				var len = data.content.length
				
				for (let i = 0; i < len; i++) {
					this.bulletList.push(new Doomm(data.content[i], parseInt(Math.random() * (this.height - 60 + 1) + 60, 10), Math
						.ceil(Math.random() * 10),
						this.getRandomColor()));
				}
			});

			var i = 0;
			class Doomm {
				constructor(text, top, time, color) {
					this.text = text;
					this.top = top;
					this.time = time;
					this.color = color;
					this.display = true;
					this.id = i++;

				}
			}
			
			
		},
		getRandomColor() {
			let rgb = []
			for (let i = 0; i < 3; ++i) {
				let color = Math.floor(Math.random() * 256).toString(16)
				color = color.length == 1 ? '0' + color : color
				rgb.push(color)
			}
			return '#' + rgb.join('')
		},
		showDoomm() {
			(this.doomm == true) ? (this.doomm = false) : (this.doomm = true);
		}


	},
	onLoad() {
		this.findDyn(1);
		uni.showLoading({
			title: '加载中',
		});
		uni.getSystemInfo({
			success: function(res) {
				this.height = res.windowHeight
			}
		});
	},
	// onShow() {
	// 	this.findDyn(1);
	// 	uni.showLoading({
	// 		title: '加载中',
	// 	});
	// },
	onReachBottom() {
		console.log("onReachBottom")
	},
	onPullDownRefresh() {
		this.loadFindDyn();
		setTimeout(function() {
			//初始化数据
			uni.stopPullDownRefresh(); //停止下拉刷新
		}, 1000);
	},
}
